.header-color {
  color: #D3D3D3; }

.primary_blue_color {
  color: #0082CD; }

.white {
  color: #FFFFFF; }

.white-background {
  background-color: #FFFFFF; }

.gray {
  background-color: #F4F4F4; }

.header .sticky {
  position: fixed;
  top: 0;
  border-radius: 0;
  width: 100%;
  z-index: 999;
  background-color: #000000; }

.header .logo-padding {
  padding-left: 65px; }

.header .logo-padding img {
  padding-top: 20px;
  padding-bottom: 20px; }

.header .nav-padding {
  padding: 10px 65px; }

.header .nav li a {
  padding: 30px 15px;
  color: #D3D3D3; }

.header .nav .active a,
.header .nav .active a:hover {
  background-color: #006AA3; }

.benefits {
  background: #2F2F2F;
  padding: 30px 10px 30px 10px; }
  .benefits .benefits-header {
    font-size: 30px; }
  .benefits .benefits-content {
    padding: 10px 100px 10px 100px; }
  .benefits .benefits-info {
    font-size: 16px; }

.contact_us {
  background-color: #FFFFFF; }
  .contact_us .padding-contact-us {
    padding: 30px; }
  .contact_us .contact_head {
    font-size: 30px;
    color: #0082CD; }
  .contact_us .contact-info {
    font-size: 16px; }
  .contact_us .details-info {
    background-color: #F2F2F2;
    padding: 10px; }
    .contact_us .details-info .details-info-text {
      font-size: 20px; }
  .contact_us .map-style {
    padding: 0; }
  .contact_us .margin-details {
    margin: 30px; }
  .contact_us .fa {
    color: #0082CD; }

.services .centered {
  background-color: black;
  padding: 10px;
  filter: alpha(opacity=50);
  /* For IE8 and earlier */
  position: absolute;
  color: #FFFFFF;
  font-size: 30px;
  width: 370px;
  top: 78%;
  left: 50%;
  background-color: black;
  background-color: rgba(0, 0, 0, 0.6);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000, endColorstr=#99000000);
  -ms-filter: "progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000, endColorstr=#99000000)";
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%); }

.services .image-font {
  font-size: 30px; }

.services .products {
  border-bottom: 15px solid #0082CD; }
  .services .products .prod-container {
    padding: 30px; }

.services_details {
  padding: 15px 80px 15px 80px; }

.footer-details {
  height: auto;
  background-color: #2F2F2F; }
  .footer-details h4 {
    color: #FFFFFF; }
  .footer-details .col-md-4 {
    padding: 30px 40px 30px 30px; }
  .footer-details .padding-left-48 {
    padding-left: 48px; }
  .footer-details .fa-padding {
    padding: 12px; }
  .footer-details .second-div .details-info {
    padding-left: 48px; }
  .footer-details .first-div .details-info {
    padding-left: 42px; }

.footer {
  height: 75px; }
  .footer .footer-fixed {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 23px 69px;
    color: #797979;
    background-color: #262626; }

#map {
  width: 100%;
  height: 280px;
  background-color: grey; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@media only screen and (max-width: 1193px) {
  .openHours {
    display: none; } }



